.video {
    position: relative; // para mantener al .encima dentro de los limites de .video
    max-height: 50rem;
    overflow: hidden;
    .encima {
        position: absolute; // absolute para colocar el .encima por medio de coordenadas
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,571699+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        // filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff', GradientType=1);
        /* IE6-9 fallback on horizontal gradient */
    }
    video {
        display: block;
        width: 100%; // el video va a fluir tomando el 100% de la pantalla
    }
}

.contenido-video {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
    z-index: 1;
    h2 {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        color: $blanco;
        font-size: 7rem;
    }
    span {
        background-color: $rosa;
        font-size: 2rem;
        padding: 1rem 2rem;
    }
}