/*---Globales---*/
html {
  font-size: 62.5%;
  /*10px = 1rem**/
  box-sizing: border-box;
  /*Snippet de Paul Irish *1 */
}

*,
*:before,
*:after {
  box-sizing: inherit;
  /* *1 */
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: black;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

/*---Utilidades---*/
.bg-amarillo {
  background-color: #e6e324ea;
}

.bg-verde {
  background-color: #07d429a4;
}

/*---Header---*/
.header {
  background-color: #07d429a4;
}
.header .contenido-header {
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #ffffff;
  margin-bottom: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444;
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.navegacion-principal a {
  color: #ffffff;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  color: #e6e324ea;
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem 0;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: rgba(5, 163, 31, 0.6431372549);
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: rgba(193, 190, 22, 0.9176470588);
}

/*---Video---*/
.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden;
}
.video .encima {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,571699+67,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(87, 22, 153, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #ffffff;
  font-size: 7rem;
}
.contenido-video span {
  background-color: #e451e9ea;
  font-size: 2rem;
  padding: 1rem 2rem;
}

/*---Festival---*/
.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  font-size: 3rem;
}
.contenido-festival .fecha {
  color: #e451e9ea;
  font-weight: 700;
}
.contenido-festival .texto {
  line-height: 1.5;
  color: black;
}

/*---Line-up---*/
.line-up {
  background-color: #e451e9ea;
  padding: 5rem 0;
}
.line-up h3,
.line-up p {
  text-align: center;
  color: #ffffff;
}
.line-up .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contendeor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contendeor .escenario {
  padding-top: 2.5rem 0;
}
.escenarios-contendeor .nombre-escenario {
  color: black;
  font-size: 2rem;
  font-weight: 300;
}
.escenarios-contendeor .nombre-escenario:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

/*---Galería---*/
.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin: 5rem auto 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}
.overlay .imagen-grande {
  max-width: 50%;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #ffffff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem;
  border-radius: 50%;
}
.btn-cerrar:hover {
  cursor: pointer;
}

/*---Boletos---*/
.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
}
.boletos .contenedor-boletos {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
.boletos .plan p {
  color: #ffffff;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
  border-spacing: 0;
}
.pase thead {
  background-color: #e451e9ea;
}
.pase thead th {
  padding: 1.25rem;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2rem;
}
.pase tbody {
  background-color: #ffffff;
}
.pase tbody td {
  padding: 1.25rem;
  border-bottom: solid #e1e1e1 1px;
}
.pase tbody tr:last-of-type td {
  border-bottom: none;
}
.pase tfoot {
  background-color: #8e1cda6e;
}
.pase tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: #ffffff;
}

/*---Footer---*/
.footer {
  background-color: #e451e9ea;
  padding: 1.25rem;
  text-align: center;
}
.footer p {
  color: #ffffff;
  margin: 0;
  font-weight: 300;
}