.line-up {
    background-color: $rosa;
    padding: $separacion 0;
    h3,
    p {
        text-align: center;
        color: $blanco;
    }
    .dia {
        text-transform: uppercase;
        margin: $separacion 0;
    }
}

.escenarios-contendeor {
    @include tablet {
        @include grid(2, $separacion);
    }
    .escenario {
        padding-top: $separacion/2 0;
    }
    .nombre-escenario {
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;
        &:after {
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
}

.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}

.rock .nombre-escenario {
    &:after {
        background-image: url(../img/icono_rock.png);
    }
}